@import url("/app/assets/fonts/Inconsolata-VariableFont_wdth,wght.ttf");
@import url("https://fonts.googleapis.com/css2?family=Inconsolata:wght@200..900&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.overflow-hidden {
  overflow: hidden;
}

.ticket.active {
  display: flex;
}

.art-ticket.active {
  display: flex;
  height: 170px;
}

.main.non-active {
  display: none;
}

body {
  background-size: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-image: url("/app/assets/images/background-desktop.png");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  font-family: "Inconsolata", serif;
  position: relative;
}
@media ((min-width: 768px) and (max-width: 991.98px)) {
  body {
    background-image: url("/app/assets/images/background-tablet.png");
  }
}
@media (max-width: 767.98px) {
  body {
    background-image: url("/app/assets/images/background-mobile.png");
  }
}
body .attribution {
  margin: 0px;
}
@media (max-width: 767.98px) {
  body .attribution {
    margin: 60px;
  }
}
body .header {
  display: flex;
  align-items: center;
  padding-bottom: 50px;
  padding-top: 20px;
  font-weight: bold;
  gap: 15px;
}
body .header .head-logo {
  height: 30px;
  width: 30px;
}
body .header .span {
  font-size: 18px;
}
body .main {
  display: flex;
  justify-content: center;
  justify-items: center;
  flex-direction: column;
  align-items: center;
  width: 590px;
  position: relative;
  z-index: 2;
}
@media (max-width: 767.98px) {
  body .main {
    width: 375px;
  }
}
body .main h1 {
  text-align: center;
  font-size: 38px;
  font-weight: bolder;
  line-height: 1.25;
  margin-bottom: 35px;
}
@media (max-width: 767.98px) {
  body .main h1 {
    font-size: 30px;
    font-weight: bolder;
    line-height: 1.25;
    margin-bottom: 25px;
    text-align: center;
  }
}
body .main .head-p {
  color: hsl(245, 15%, 58%);
  text-align: center;
  margin-bottom: 35px;
  font-size: 20px;
}
@media (max-width: 767.98px) {
  body .main .head-p {
    font-size: 14px;
  }
}
@media (max-width: 767.98px) {
  body .main form {
    width: 90%;
  }
}
body .main .upload-file {
  padding-top: 10px;
  background-color: rgba(75.11875, 72.2925, 106.2075, 0.25);
  border-radius: 10px;
  border: 1.5px dashed hsl(0, 0%, 100%);
}
body .main .upload-file img {
  padding: 10px;
  background-color: rgba(75.11875, 72.2925, 106.2075, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 10px;
  margin-bottom: 20px;
}
body .main .upload-file .div-inp {
  margin-bottom: 15px;
  display: flex;
  gap: 10px;
}
body .main .upload-file .div-inp .remove-img {
  font-family: "Inconsolata", serif;
  cursor: pointer;
  background-color: rgba(75.11875, 72.2925, 106.2075, 0.5);
  color: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
}
body .main .upload-file .div-inp .remove-img:is(:hover, :focus) {
  text-decoration: underline;
}
body .main .upload-file .div-inp .change-img-lab {
  font-family: "Inconsolata", serif;
  cursor: pointer;
  background-color: rgba(75.11875, 72.2925, 106.2075, 0.5);
  color: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 5px;
  padding: 5px;
}
body .main .upload-file .div-inp .change-img-lab:is(:hover, :focus) {
  text-decoration: underline;
}
body .main article {
  margin-bottom: 25px;
  width: 460px;
}
@media (max-width: 767.98px) {
  body .main article {
    width: 335px;
  }
}
body .main article p {
  font-size: 16px;
  color: hsl(252, 6%, 83%);
  margin-bottom: 10px;
}
body .main article input {
  border: 1px solid white;
  border-radius: 10px;
  padding: 15px;
  width: 100%;
  height: 45px;
  background-color: rgba(75.11875, 72.2925, 106.2075, 0.25);
  color: white;
}
body .main .artcile-upload-file input {
  inset: 10px solid white;
}
body .main .artcile-upload-file label {
  display: flex;
  align-items: center;
  flex-direction: column;
}
body .main .artcile-upload-file .span-inf {
  color: hsl(245, 15%, 58%);
}
body .main .artcile-upload-file .icon-info {
  height: 15px;
  width: 15px;
}
body .main .btn-gener-tckt {
  width: 100%;
  height: 55px;
  color: black;
  font-size: 16px;
  background-color: hsl(7, 88%, 67%);
  border: 0px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
  position: relative;
  z-index: 2;
}
body .main .info {
  display: flex;
  gap: 10px;
  padding: 15px 0;
}
body .img_circle {
  position: absolute;
  left: 2.5rem;
  top: -5rem;
}
body .img_circle2 {
  position: absolute;
  right: 20.25rem;
  top: 26rem;
}
body .img_lineBot {
  background-image: url("/app/assets/images/pattern-squiggly-line-bottom-desktop.svg");
  position: absolute;
  left: 0;
  bottom: 0;
}
@media (max-width: 767.98px) {
  body .img_lineBot {
    width: 100%;
    background-image: url("/app/assets/images/pattern-squiggly-line-bottom-mobile");
  }
}
@media ((min-width: 768px) and (max-width: 991.98px)) {
  body .img_lineBot {
    width: 100%;
    background-image: url("/app/assets/images/pattern-squiggly-line-bottom-mobile");
  }
}
body .img_lineTop {
  position: absolute;
  top: 3rem;
  right: 0;
}
body .img_lines {
  width: 100%;
  position: absolute;
  top: -11rem;
  height: 100%;
}

.art-ticket {
  display: none;
  flex-direction: column;
  gap: 20px;
  text-align: center;
}
@media (max-width: 767.98px) {
  .art-ticket {
    margin-bottom: 0px;
  }
}
.art-ticket .full-name2 {
  background: linear-gradient(90deg, hsl(7, 86%, 67%), hsl(0, 0%, 100%));
  color: transparent;
  -webkit-background-clip: text;
}
.art-ticket .email-span {
  color: hsl(7, 71%, 60%);
}

.ticket {
  display: none;
  width: 600px;
  height: 280px;
  gap: 180px;
  position: relative;
}
@media (max-width: 767.98px) {
  .ticket {
    width: 350px;
    height: 170px;
  }
}
.ticket .left-tick {
  display: grid;
  /* border-bottom-right-radius: 10px; */
  /* border-top-right-radius: 10px; */
  padding: 25px;
  gap: 55px;
  width: 950px;
}
@media (max-width: 767.98px) {
  .ticket .left-tick {
    display: grid;
    padding: 10px;
    gap: 0px;
    width: 80px;
    height: 185px;
  }
}
.ticket .left-tick .head-ticket {
  display: flex;
  gap: 15px;
  margin-bottom: 50px;
}
@media (max-width: 767.98px) {
  .ticket .left-tick .head-ticket {
    height: 50px;
    margin-bottom: 0px;
  }
}
@media (max-width: 767.98px) {
  .ticket .left-tick .data-tick {
    width: max-content;
  }
}
.ticket .left-tick .data-tick .data-tick-p {
  font-size: 28px;
  color: hsl(0, 0%, 100%);
  margin-bottom: 5px;
  font-weight: bold;
}
.ticket .left-tick .data-tick .data-p {
  font-size: 14px;
  color: rgba(75.11875, 72.2925, 106.2075, 0.75);
}
.ticket .left-tick .footer-ticket {
  display: flex;
  gap: 15px;
  align-items: center;
}
@media (max-width: 767.98px) {
  .ticket .left-tick .footer-ticket {
    height: 50px;
    width: max-content;
  }
}
.ticket .left-tick .footer-ticket img {
  height: 80px;
  width: 80px;
  border-radius: 10px;
}
.ticket .left-tick .footer-ticket .full-name {
  font-size: 24px;
  color: hsl(0, 0%, 100%);
  margin-bottom: 10px;
}
.ticket .left-tick .footer-ticket .email-p {
  color: rgba(75.11875, 72.2925, 106.2075, 0.75);
  font-size: 13;
  display: flex;
  gap: 5px;
  align-items: center;
}
.ticket .right-tick {
  border-bottom-left-radius: 10px;
  border-top-left-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ticket .right-tick span {
  left: 82%;
  position: absolute;
  font-size: 20px;
  padding: 40px;
  color: rgba(75.11875, 72.2925, 106.2075, 0.75);
  writing-mode: vertical-rl;
  text-orientation: none;
}
@media (max-width: 767.98px) {
  .ticket .right-tick span {
    padding: 20px;
  }
}
.ticket .svg-ticket {
  width: 600px;
  height: 280px;
  position: absolute;
  left: 0px;
}
@media (max-width: 767.98px) {
  .ticket .svg-ticket {
    width: 350px;
    height: 170px;
  }
}